@media (min-width: 768px) {
    .card-box-a:hover .card-overlay-a-content {
        bottom: 60px;
    }
}

@media (min-width: 768px) and (min-width: 768px) {
    .card-box-a:hover .card-overlay-a-content {
        bottom: 60px;
    }
}

@media (min-width: 768px) and (min-width: 992px) {
    .card-box-a:hover .card-overlay-a-content {
        bottom: 64px;
    }
}

@media (min-width: 768px) {
    .card-box-a:hover .card-body-a {
        padding-bottom: 1rem;
    }
}

@media (max-width: 767px) {
    .card-box-a .card-overlay-a-content {
        bottom: 65px;
    }
    .card-box-a .card-body-a {
        padding-bottom: 1rem;
    }
}

.card-overlay-a-content {
    transition: all .5s ease-in;
    position: absolute;
    width: 100%;
    bottom: 0;
    z-index: 2;
}

@media (min-width: 768px) {
    .card-overlay-a-content {
        bottom: -20px;
    }
}

@media (min-width: 992px) {
    .card-overlay-a-content {
        bottom: 0;
    }
}

.card-header-a {
    padding: 0 1rem;
}

.card-header-a .card-title-a {
    color: #fff;
    margin-bottom: 0;
    padding-bottom: 0.7rem;
}

@media (min-width: 768px) {
    .card-header-a .card-title-a {
        font-size: 1.3rem;
    }
}

@media (min-width: 992px) {
    .card-header-a .card-title-a {
        font-size: 2rem;
    }
}

.card-header-a .card-title-a a {
    color: #fff;
    text-decoration: none;
}

.card-body-a {
    z-index: 2;
    transition: all .5s ease-in;
    padding: 0 1rem 2rem 1rem !important;
}

.card-body-a .price-box {
    padding-bottom: 0.5rem;
}

.price-a {
    color: #fff;
    padding: 0.6rem 0.8rem;
    border: 2px solid #c48d17;
    border-radius: 50px;
    text-transform: uppercase;
    letter-spacing: 0.03em;
}

.card-footer-a {
    width: 100%;
    position: absolute;
    z-index: 2;
    background-color: #c48d17;
}

.card-info {
    list-style: none;
    margin-bottom: 0;
    padding: 0.5rem 0;
}

.card-info .card-info-title {
    font-size: 1rem;
    color: #313131;
}

@media (min-width: 768px) {
    .card-info .card-info-title {
        font-size: .9rem;
    }
}

@media (min-width: 992px) {
    .card-info .card-info-title {
        font-size: 1rem;
    }
}

.card-info li span {
    color: #fff;
    font-size: 0.9rem;
}

@media (min-width: 768px) {
    .card-info li span {
        font-size: 0.8rem;
    }
}

@media (min-width: 992px) {
    .card-info li span {
        font-size: 0.9rem;
    }
}





